@import url(https://fonts.googleapis.com/css2?family=Quicksand:wght@300;400;500;600;700&display=swap);
/* base styles */
* {
  margin: 0;
  font-family: "Quicksand";
  /* background-color: #212529 !important; */
  color: white;
}
.App{
  background-color: #212529;
}
.navbar {
  padding: 20px;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  max-width: 800px;
  margin: 0 auto;
  border-bottom: 1px solid #f2f2f2;
}
.navbar h1 {
  color: #f1356d;
}
.navbar .links {
  margin-left: auto;
}
.navbar a {
  margin-left: 16px;
  text-decoration: none;
  padding: 6px;
}
.content {
  max-width: 700px;
  margin: 0px auto;
  padding: 20px;
}

/* blog previews / list */

.interview-preview:hover {
  border-radius: 5px;
  box-shadow: 1px 3px 5px rgba(0, 0, 0, 0.1);
}
.interview-preview h2 {
  font-size: 20px;
  color: #f1356d;
  margin-bottom: 8px;
}

.red-btn {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  color: white;
  background-color: #f1456d !important;
  /* border-radius: 6px; */
  cursor: pointer;
}

.green-btn {
  color: white;
  background-color: #18a558 !important;
  /* border-radius: 6px; */
  padding: 2px;
 /* margin-bottom: 5px; */
 /* margin-right: 10px; */

  cursor: pointer;
}


.blue-btn {
  color: white;
  background-color: #0064ff;
  border-radius: 6px;
}

.create {
  max-width: 700px;
  margin: 0 auto;
}
.create label {
  text-align: left;
  margin-bottom: 5px;
  margin-top: 15px;
  display: block;
}
.create h2 {
  font-size: 20px;
  color: #f1356d;
  margin-bottom: 30px;
}
.create input,
.create textarea,
.create select {
  width: 100%;
  padding: 6px 10px;
  margin: 10px 0;
  border: 1px solid #ddd;
  box-sizing: border-box;
  display: block;
}
.create button {
  background: #f1356d;
  margin-top: 20px;
  color: #fff;
  border: 0;
  padding: 8px;
  border-radius: 8px;
  cursor: pointer;
}

.links{
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction:row;
            flex-direction:row;
}


@media screen and (max-width: 500px) {
    .links {
      -webkit-flex-direction: column;
              flex-direction: column;
    }
  }
